<template>
  <section v-if="seen">
    <div class="con-top counsel-info">
      <p class="text">  ?<br>  , ?</p>
      <p class="pt10"> FINSET<br> </p>
      <a @click="openInfo()">?</a>
    </div>
    <div v-if="counselList.length == 0" class="nodata">  </div>
    <div v-else class="box-list counsel-list noMG">
      <template v-for="counselInfo in counselList">
        <!--  -->
        <div v-if="counselInfo.cd_counsel_status=='1'" :key="counselInfo.index" class="item">
          <div class="top">
            <p>{{counselInfo.dt_apply}}</p>
            <p><a @click="viewResult(counselInfo.counsel_seq, counselInfo.cd_counsel_status)"></a></p>
          </div>
          <div class="result apply">
            <p class="title">  </p>
            <p class="text">{{counselInfo.nm_person}}   .</p>
          </div>
        </div>
        <!--   -->
        <div v-else-if="counselInfo.cd_counsel_status=='2'" :key="counselInfo.index" class="item">
          <div class="top">
            <p>{{counselInfo.dt_pre_counsel}}</p>
            <p><a @click="viewResult(counselInfo.counsel_seq, counselInfo.cd_counsel_status)"></a></p>
          </div>
          <div class="result ing">
            <p class="title"> </p>
            <p class="text">{{counselInfo.nm_person}}  .</p>
          </div>
        </div>
        <!--  -->
        <div v-else :key="counselInfo.index" class="item">
          <div class="top">
            <p>{{counselInfo.dt_counsel}}</p>
            <p><a @click="viewResult(counselInfo.counsel_seq, counselInfo.cd_counsel_status)"></a></p>
          </div>
          <div class="result done">
            <p class="title"> </p>
            <p class="text">{{counselInfo.nm_person}}  .</p>
          </div>
        </div>
      </template>
    </div>

    <div class="btn-wrap float">
      <a @click="$router.push('/credit/counselReqStep1')" class="solid blue box"> </a>
    </div>

    <vue-modal transitionName="zoom-in" name="info-modal" v-on:popclose="closeInfo()">
      <CounselInfo slot="body" v-on:popclose="closeInfo()"></CounselInfo>
    </vue-modal>
  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";
import ko from "vee-validate/dist/locale/ko.js";

import CounselInfo from "./CounselInfo.vue";

export default {
  name: "creditCounselMain",
  data() {
    return {
      seen: false,
      counselList: []
    };
  },
  components: {
    CounselInfo: CounselInfo
  },
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "";
  },
  created() {
    this.listCreditCounselMain();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    openInfo: function() {
      var _this = this;
      _this.$modals.show("info-modal");
    },
    closeInfo: function() {
      var _this = this;
      _this.$modals.hide("info-modal");
    },
    //list
    listCreditCounselMain: function() {
      var _this = this;
      this.$http
        .get("/m/credit/listCreditCounselMain.json", {
          params: {}
        })
        .then(response => {
          _this.counselList = response.data.counselList;

          _this.seen = true;
        })
        .catch(e => {
          this.$toast.center(ko.messages.error);
        });
    },
    //
    viewResult: function(counsel_seq, cd_counsel_status) {
      this.$router.push({
        name: "creditCounselResult",
        query: {
          counsel_seq: counsel_seq,
          cd_counsel_status: cd_counsel_status
        }
      });
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



