<template>
  <section v-if="seen">
    <div class="detail-list mt30 pb90">
      <dl>
        <dd>
          <p></p>
          <p><input type="text" :value="counselInfo.nm_person" readonly="readonly"></p>
        </dd>
        <dd>
          <p></p>
          <p><input type="text" :value="' '+counselInfo.age+'('+counselInfo.sex+')'" readonly="readonly"></p>
        </dd>
      </dl>
      <dl>
        <dt></dt>
        <dd>
          <p></p>
          <p><input type="text" :value="counselInfo.grade_credit+' '" readonly="readonly"></p>
        </dd>
        <dd>
          <p></p>
          <p><input type="text" :value="counselInfo.bal_overdue+' '" readonly="readonly"></p>
        </dd>
      </dl>
      <dl>
        <dt></dt>
        <dd>
          <p></p>
          <p><input type="text" :value="counselInfo.amt_contract+' '" readonly="readonly"></p>
        </dd>
        <dd>
          <p></p>
          <p><input type="text" :value="counselInfo.amt_remain+' '" readonly="readonly"></p>
        </dd>
        <dd>
          <p></p>
          <p><input type="text" :value="counselInfo.cur_mm_amt_repay+' '" readonly="readonly"></p>
        </dd>
      </dl>
      <dl>
        <dt></dt>
        <dd>
          <p></p>
          <p><input type="text" :value="counselInfo.cnt_card_use+' '" readonly="readonly"></p>
        </dd>
        <dd>
          <p></p>
          <p><input type="text" :value="counselInfo.amt_card_total+' '" readonly="readonly"></p>
        </dd>
      </dl>
      <div class="btn-wrap float">
        <a @click="$router.push('/credit/counselReqStep3')" class="solid blue box"></a>
      </div>
    </div>

  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";
import ko from "vee-validate/dist/locale/ko.js";

export default {
  name: "creditCounselReqStep2",
  data() {
    return {
      seen: false,
      counselInfo: ""
    };
  },
  components: {},
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "()";
  },
  created() {
    this.getCounselReqStep2Info();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    //
    getCounselReqStep2Info: function() {
      var _this = this;
      this.$http
        .get("/m/credit/getCreditCounselReqStep2Info.json", {
          params: {}
        })
        .then(response => {
          _this.counselInfo = response.data.counselInfo;

          _this.seen = true;
        })
        .catch(e => {
          this.$toast.center(ko.messages.error);
        });
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



