<template>
  <section v-if="seen">
    <div class="assets-top">
      <div class="wrap">
        <div class="item">
          <p class="key"><em>()</em></p>
          <p class="value">{{('0'==assetsSumAmt)? '-' : formatNumber(assetsSumAmt)}}</p>
        </div>
        <div class="item">
          <p class="key"><em>()</em></p>
          <p class="value">{{(debtSumAmt==null)? '-' : formatNumber(debtSumAmt)}}</p>
        </div>
      </div>
    </div>

    <div class="banner-wrap owl-carousel">
      <carousel :perPage=1>
        <slide class="item">
          <a @click="goMenu('ctrlFcLink')">
            <div class="banner">
              <div class="left">
                <p class="key">  </p>
                <p class="value">  <br></p>
              </div>
              <div class="right">
                <img src="../../assets/images/assets/main_banner1.png" alt="" />
              </div>
            </div>
          </a>
        </slide>
        <!--  -->
        <slide class="item">
          <a>
            <div class="banner">
              <div class="left">
                <p class="key">  </p>
                <p class="value">  <br>  </p>
              </div>
              <div class="right">
                <img src="../../assets/images/assets/main_banner2.png" alt="" />
              </div>
            </div>
          </a>
        </slide>
        <!--  -->
        <slide class="item">
          <a>
            <div class="banner">
              <div class="left">
                <p class="key"></p>
                <p class="value">  </p>
              </div>
              <div class="right">
                <img src="../../assets/images/assets/main_banner3.png" alt="" />
              </div>
            </div>
          </a>
        </slide>
      </carousel>
    </div>

    <div class="list02 box-list pb90">

      <div @click="goMenu('bank')" class="item">
        <div class="flex">
          <p class="corp big"><em>{{assetsBankInfo.cnt_account}}</em></p>
          <p class="number big">{{(assetsBankInfo.sum_amt_balance == null)? '-' : formatNumber(assetsBankInfo.sum_amt_balance)}}<em></em></p>
        </div>
      </div>
      <div @click="goMenu('stock')" class="item">
        <div class="flex">
          <p class="corp big">(//CMA)<em>{{assetsStockInfo.cnt_account}}</em></p>
          <p class="number big">{{(assetsStockInfo.sum_amt_stock == null)? '-' : formatNumber(assetsStockInfo.sum_amt_stock)}}<em></em></p>
        </div>
      </div>
      <!--
      <div class="item">
        <div class="flex">
          <p class="corp big"><em>3</em></p>
          <p class="number big">46,570,000<em></em></p>
        </div>
      </div>
      -->
      <div @click="goMenu('etc')" class="item">
        <div class="flex">
          <p class="corp big"><em>{{assetsEtcInfo.cnt_item}}</em></p>
          <p class="number big">{{(assetsEtcInfo.sum_amt_evaluation == null)? '-' : formatNumber(assetsEtcInfo.sum_amt_evaluation)}}<em></em></p>
        </div>
      </div>
      <button class="btn-spend-add" @click="$router.push('/assets/dirInput')"></button>
    </div>

  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";

import ko from "vee-validate/dist/locale/ko.js";

export default {
  name: "assetsMain",
  data() {
    return {
      seen: false,
      assetsSumAmt: "", //
      debtSumAmt: "", //
      assetsBankInfo: "", //
      assetsStockInfo: "", //
      assetsEtcInfo: "" //
    };
  },
  components: {},
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "main";
    this.$store.state.header.active = "assets";
  },
  created() {
    this.getAssetsMainInfo();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    //  
    getAssetsMainInfo: function() {
      var _this = this;
      this.$http
        .get("/m/assets/getAssetsMainInfo.json", {
          params: {}
        })
        .then(response => {
          _this.assetsSumAmt = response.data.assetsSumAmt;
          _this.debtSumAmt = response.data.debtSumAmt;
          _this.assetsBankInfo = response.data.assetsBankInfo;
          _this.assetsStockInfo = response.data.assetsStockInfo;
          _this.assetsEtcInfo = response.data.assetsEtcInfo;

          _this.seen = true;
        })
        .catch(e => {
          this.$toast.center(ko.messages.error);
        });
    },
    formatNumber: function(data) {
      return Common.formatNumber(data);
    },
    //
    goMenu: function(menu) {
      //
      if ("ctrlFcLink" == menu) {
        this.$router.push({
          name: "scrapCtrlFcLink",
          params: { tab: "stock" }
        });
        //
      } else if ("bank" == menu) {
        this.$router.push({
          name: "assetsBankMain",
          params: {}
        });
        //
      } else if ("stock" == menu) {
        this.$router.push({
          name: "assetsStockMain",
          params: {}
        });
      } else if ("etc" == menu) {
        this.$router.push({
          name: "assetsEtcMain",
          params: {}
        });
      }
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



