<template>
  <section v-if="seen">
    <div class="container">
      <ul class="debt-modify">
        <li>
          <div>
            <p class="key"></p>
            <p>
              <multiselect v-model="yn_wedding" ref="yn_wedding" placeholder="" track-by="text" label="text" :options="ynWeddingOptions" :searchable="false" :allow-empty="false" @select="selectYnWedding()" v-validate="'required'" data-vv-name=''>
              </multiselect>
            </p>
          </div>
          <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
        </li>
        <li>
          <div>
            <p class="key"></p>
            <p>
              <multiselect v-model="cd_family_cnt" ref="cd_family_cnt" placeholder="" track-by="text" label="text" :options="options_family_cnt" :searchable="false" :allow-empty="false" @select="selectFamilyCnt()" v-validate="'required'" data-vv-name=''>
              </multiselect>
            </p>
          </div>
          <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
        </li>
        <li>
          <div>
            <p class="key"></p>
            <p>
              <multiselect v-model="cd_living" ref="cd_living" placeholder="" track-by="text" label="text" :options="options_living" :searchable="false" :allow-empty="false" @select="selectLiving()" v-validate="'required'" data-vv-name=''>
              </multiselect>
            </p>
          </div>
          <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
        </li>
        <li>
          <div>
            <p class="key"></p>
            <p>
              <multiselect v-model="cd_job" ref="cd_job" placeholder="" track-by="text" label="text" :options="options_job" :searchable="false" :allow-empty="false" @select="selectJob()" v-validate="'required'" data-vv-name=''>
              </multiselect>
            </p>
          </div>
          <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
        </li>
        <li>
          <div>
            <p class="key"></p>
            <p><input type="number" id="amt_mm_income" v-model="amt_mm_income" placeholder="" v-html="counselInfo.amt_income_total" @change="changeIncome()" v-validate="'required|numeric|max:10'" data-vv-name=''></p>
          </div>
          <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
        </li>
        <li>
          <div>
            <p class="key"></p>
            <p><input type="number" id="amt_mm_expense" v-model="amt_mm_expense" placeholder="" v-html="counselInfo.amt_expense_total" @change="changeExpense()" v-validate="'required|numeric|max:10'" data-vv-name=''></p>
          </div>
          <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
        </li>
      </ul>
      <div v-if="isShowBtn" class="btn-wrap float">
        <a @click="goCounselReqStep4()" class="solid blue box"></a>
      </div>
    </div>

  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";
import ko from "vee-validate/dist/locale/ko.js";

export default {
  name: "creditCounselReqStep3",
  data() {
    return {
      seen: false,
      ynWeddingOptions: [
        { text: "", value: "N" },
        { text: "", value: "Y" }
      ],
      yn_wedding: "",
      options_family_cnt: [],
      cd_family_cnt: "",
      options_living: [],
      cd_living: "",
      options_job: [],
      cd_job: "",
      amt_mm_income: "",
      amt_mm_expense: "",
      counselInfo: "",
      isShowBtn: false
    };
  },
  components: {},
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "()";
  },
  created() {
    this.options_family_cnt = Common.makeOptions("cd_family_cnt", "");
    this.options_living = Common.makeOptions("cd_living", "");
    this.options_job = Common.makeOptions("cd_job", "");
    this.getCounselReqStep3Info();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    //
    getCounselReqStep3Info: function() {
      var _this = this;
      this.$http
        .get("/m/credit/getCreditCounselReqStep3Info.json", {
          params: {}
        })
        .then(response => {
          var counselInfo = response.data.counselInfo;
          _this.amt_mm_income = counselInfo.amt_income_total;
          _this.amt_mm_expense = counselInfo.amt_expense_total;
          _this.counselInfo = counselInfo;

          _this.seen = true;
        })
        .catch(e => {
          this.$toast.center(ko.messages.error);
        });
    },
    //
    selectYnWedding: function() {
      var _this = this;
      setTimeout(function() {
        _this.$refs.cd_family_cnt.$el.focus();
      }, 100);
    },
    selectFamilyCnt: function() {
      var _this = this;
      setTimeout(function() {
        _this.$refs.cd_living.$el.focus();
      }, 100);
    },
    selectLiving: function() {
      var _this = this;
      setTimeout(function() {
        _this.$refs.cd_job.$el.focus();
      }, 100);
    },
    selectJob: function() {
      var _this = this;
      setTimeout(function() {
        $("#amt_mm_income").focus();
        _this.showBtn();
      }, 100);
    },
    changeIncome: function() {
      var _this = this;
      setTimeout(function() {
        _this.showBtn();
      }, 100);
    },
    changeExpense: function() {
      var _this = this;
      setTimeout(function() {
        _this.showBtn();
      }, 100);
    },
    showBtn: function() {
      var _this = this;
      if ("" != _this.amt_mm_income && "" != _this.amt_mm_expense) {
        _this.isShowBtn = true;
      } else {
        _this.isShowBtn = false;
      }
    },
    // 
    goCounselReqStep4: function() {
      var _this = this;

      this.$validator.validateAll().then(res => {
        if (res) {
          this.$router.push({
            name: "creditCounselReqStep4",
            query: {
              yn_wedding: _this.yn_wedding.value,
              cd_family_cnt: _this.cd_family_cnt.value,
              cd_living: _this.cd_living.value,
              cd_job: _this.cd_job.value,
              amt_mm_income: _this.amt_mm_income,
              amt_mm_expense: _this.amt_mm_expense
            }
          });
        } else {
          this.$toast.center(ko.messages.require);
        }
      });
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



